body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.colorLight {
    background-color: #FBFAFA;
}

.colorLightAccent {
    background-color: #938588;
}

.colorDark {
    background-color: #3A5D56;
}

.colorDarkAccent {
    background-color: #7F7A89;
}



.colorComp {
    background-color: #9D9DA7;
}

.compColor {
    color: #3A5D56;
}

.black {
    background-color: #19180A;
}

.txtfont {
    color: #e6e6e6;
}

.mainfont {
    color: #E8E8E8;
}

.back {
    background: -webkit-linear-gradient(rgba(25,24,10,0.4), rgba(25,24,10, 1)), url(/Portfolio/static/media/background4.e327160b.jpg) no-repeat fixed;
    background: linear-gradient(rgba(25,24,10,0.4), rgba(25,24,10, 1)), url(/Portfolio/static/media/background4.e327160b.jpg) no-repeat fixed;
    background-size: 100%;
}
.layer {
    background-image: -webkit-linear-gradient(rgb(188,74, 104), rgb(188,74,104));
    background-image: linear-gradient(rgb(188,74, 104), rgb(188,74,104));
}
.box {

}

.borderRadius {
    border-width: 4rem;
}

::-webkit-input-placeholder {
    color: #e6e6e6;
}

::-ms-input-placeholder {
    color: #e6e6e6;
}

::placeholder {
    color: #e6e6e6;
}

/*transform-origin: 50% 50%;*/
.toolbar {
    background-color: #000000;
    color: #000000;
}

.btn {
    justify-content: flex-end;
}

.content {
    margin-top: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
}
